@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  font-family: 'Quicksand';
  color : #333;
}

.navbar{
padding:20px;
display :flex;
align-items : center;
max-width:600px;
margin : 0 auto;
border-bottom: 1px solid #f2f2f2;
}

.navbar h1{
  color:#f1356d;
}

.navbar .links{
  margin-left:auto;
}

.navbar a{
  margin-left:16px;
  text-decoration:none;
  padding : 6px;
}

.navbar a:hover{
  color:#f1356d;
}

.content{
  max-width:600px;
  margin : 40px auto;
  padding : 20px;
}

.home h1{
  margin-bottom:3em;
}

/* blog previews list */
.blog-preview{
  padding : 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover{
  box-shadow: 1px 3px 5px rgba(0,0,0,1);
}
.blog-preview h2{
font-size: 20px;
color: #f1356d;
margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
}
.blog-details h2{
    font-size: 20px;
    color:#f1356d;
    margin-bottom:10px;
}
.blog-details div{
  margin : 20px 0;
}
/* create new blog */
.create{
  max-width: 400px;
  margin : 0 auto;
  text-align: center;
}
.create label{
  text-align: center;
  display: block;
}
.create h2{
  font-size: 20px;
  color : #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin : 10px 0;
  border : 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button{
  background: #f1356d;
  color : #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor : pointer;
}
.not-found h2 {
  display: block;
  font-size: 1.5em;
  -webkit-margin-before: 0.83em;
          margin-block-start: 0.83em;
  -webkit-margin-after: 0.83em;
          margin-block-end: 0.83em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
  margin-bottom: 40px;
}
.not-found p{
  margin-bottom: 5px;
}
.not-found a:hover{
  color:#f1356d;
}
